export default {
  name: 'Header',
  props: {
    title: {type: String, default: ''},
    hideBackButton: {type: Boolean, default: false},
    onBack: {type: Function}
  },
  methods: {
    onBackClicked () {
      if (this.$props.onBack) {
        this.$props.onBack()
      } else {
        window.history.back()
      }
    }
  }
}



