/**
 *
 */

const TextUtil = {
  trim (text) {
    // 清除掉收尾空格
    return text.replace(/^(\s|\u00A0)+/, '').replace(/(\s|\u00A0)+$/, '')
  },
  isIdCardValid (idCard) {
    // 身份证号码是否合法
    if (idCard.length < 1) {
      return false
    }
    if (!(/(^\d{15}$)|(^\d{17}([0-9]|X)$)/.test(idCard))) {
      return false
    }
    return true
  },
  isCNNameValid (name) {
    // 中文名是否合法
    if (name.length < 1) {
      return false
    }
    if (!(/^[\u4e00-\u9fa5]+[·]?[\u4e00-\u9fa5]+$/.test(name))) {
      return false
    }
    return true
  },
  isPhoneValid (phone) {
    // 中文名是否合法
    if (phone.length < 1) {
      return false
    }
    if (!(/^1[34578]\d{9}$/.test(phone))) {
      return false
    }
    return true
  },
  getQueryString (name) {
    // 获取url参数
    let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
    let r = window.location.search.substr(1).match(reg)
    if (r != null) return r[2]
    return null
  }
}

export default TextUtil



